* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: rgb(63, 63, 63);
  font-family: "Poppins", sans-serif;
  height: auto;
}
.Main_Cntnr {
  background-color: white;
  height: 100vh;
  width: 100%;
  display: flex;
  position: relative;
}
.logobox_Settings {
  width: 17%;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  background-color: whitesmoke;
  flex-direction: column;
  box-shadow: 0 0 4px;
}
.logo {
  margin: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  background-color: #fff;
}
.Info_Accs {
  display: flex;
  justify-content: flex-start;
  margin: 20% 0 0 5%;
  flex-direction: column;
}
.Acc_Info,
.Comp_Info,
.Acc_lvl {
  padding: 6px 35px 6px 6px;
  margin: 4px 0;
}
.Acc_Info:hover,
.Comp_Info:hover,
.Acc_lvl:hover {
  border-radius: 20px;
  border: 1px solid rgba(128, 128, 128, 0.753);
  background-color: #fff;
}
.Acc_Info a,
.Comp_Info a,
.Acc_lvl a {
  text-decoration: none;
  color: #334d6e;
}

.leftbtns_Settings {
  margin-top: 100%;
}
.Emailbtn_Settings {
  width: 140px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #008bdf;
  color: #fff;
  border: none;
  margin: 13px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}

.Supportbtn_Settings {
  width: 140px;
  cursor: pointer;
  background-color: #c4dceb;
  color: #192a3e;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 13px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}
.lftmail_Settings .fa-envelope,
.lftphone_Settings .fa-phone-alt {
  padding-right: 8px;
  color: #192a3e;
}
.lftmail_Settings a {
  color: #192a3e;
  text-decoration: none;
}
.lftmail_Settings,
.lftphone_Settings {
  display: flex;
  align-items: center;
  justify-content: center;
}
.lftphone_Settings {
  color: #192a3e;
  text-align: center;
  cursor: pointer;
}

.NavContainer_Setings {
  width: 83%;
  height: auto;
  position: absolute;
  left: 17%;
}
.navbar_Settings {
  height: 50px;
  width: 82.5%;
  position: fixed;
  left: 17.1%;
  z-index: 1;
  display: flex;
  box-shadow: 0 1px 4px grey;
  background-color: whitesmoke;
  align-items: center;
  justify-content: flex-end;
}
.list {
  display: flex;
}

.list li {
  margin: 0 30px 0 10px;
  list-style: none;
}
.list li a {
  text-decoration: none;
  color: #192a3e;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.list li a .fas,
.list li a .far {
  padding-right: 5px;
}

.whole {
  width: 100%;
  position: absolute;
  top: 52px;
}
.My_Js_My_As {
  display: flex;
  align-items: center;
  font-size: 1.2em;
  padding-bottom: 2px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.527);
}
.my_job {
  background-color: rgba(128, 128, 128, 0.527);
  color: grey;
  padding-left: 5px;
  display: flex;
  height: 70px;
  align-items: center;
}
.my_job h1,
.my_acc h1 {
  font-size: 1.5em;
  font-weight: 100;
  cursor: pointer;
}
.arrow {
  width: 40px;
  height: 55px;
  font-weight: 100;
  line-height: 70px;
  background-color: #fff;
  border-left: 40px solid rgba(128, 128, 128, 0.527);
  border-top: 35px solid transparent;
  border-bottom: 35px solid transparent;
}
.my_acc {
  margin-left: 20px;
  color: grey;
}
.Acc_cntnr {
  width: 42%;
}
.Acnt_Info h2,
.Pswrd_Sec h2 {
  font-size: 1.5em;
  font-weight: 100;
  border-bottom: 1px solid rgba(21, 179, 228, 0.527);

  color: grey;
  margin-left: 20px;
  line-height: 50px;
}
.form,
.Pswd_form {
  width: 70%;
  color: rgb(63, 63, 63);
  font-size: 16px;
  padding: 10px 0;
  margin: 20px auto;
}
.form > div,
.Pswd_form > div {
  padding: 10px 0;
  display: flex;
  align-items: center;
}
.form > div label,
.Pswd_form > div label {
  width: 160px;
  text-align: right;
}
.form > div input,
.Pswd_form > div input {
  padding: 4px;
  margin-left: 5px;
  font-size: 15px;
  border: 0.1px solid grey;
  border-radius: 4px;
}
.Chck_bx {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  margin: 10px 0 20px 0;
}
.Chck_bx input {
  margin-right: 3px;
}
.Updt_btn,
.Chng_btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0 20px 0;
}
.Updt_btn button,
.Chng_btn button {
  background: #76cf21;
  font-size: 14px;
  border: none;
  outline: none;
  border-radius: 5px;
  padding: 8px;
  color: #fff;
  font-weight: 100;
}

